
*{

}
input{
    width: 100%;
    font-size: 1.2rem;
    color: blue;
    outline:auto;
    border: none;
}
.card-grid{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
}
.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom:10px;
    margin-right: 10px;
}
.card p{
    padding: 5px;
    text-align: center;
}
.card img{
    max-width: 170px;
}
